.main {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
}

.inputForm {
  text-align: center;
}

.btns {
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  font-size: 30px;
  margin: 20px 0;
}

.buttonDiv {
  display: flex;
  align-items: center;
  justify-content: center;
  vertical-align: middle;
}

.inpuBox {
  font-size: 20px;
}
.inputBtn {
  background: #ffffff;
  /* width: 10px;
  height: 10px; */
  border: none;
  cursor: pointer;
  margin: 0 10px;
}

.img {
  width: 30px;
  height: 30px;
  vertical-align: middle;
}

.btn {
  margin: 0 10px;
  cursor: pointer;
  transition: 0.5s;
}
.scheduleList {
  display: flex;
  flex-direction: column;
  text-align: center;
  margin: 20px 0;
  width: 100%;
}

.scheduleItem {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 20px;
  margin: 10px 0;
  width: 90%;
}

.content {
  width: 80%;
  justify-self: center;
  transition: 0.5s;
}

.deleteBtn {
  /* margin-left: 20px;/ */
  cursor: pointer;
  width: 10%;
  justify-self: flex-end;

  /* justify-content: center; */
}

.deleteBtn::after {
  content: "\00d7";
}

.days {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  font-size: 25px;
  margin: 40px 0;
}

.dayBtn {
  cursor: pointer;
  transition: 0.2s;
}

.dayBtn:first-child {
  color: red;
}

.dayBtn:last-child {
  color: blue;
}
.colorbox {
  width: 40px;
  height: 20px;
}

.addDay {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 20px 0;
}

.DayInput {
  font-size: 15px;
  margin: 0 10px;
}

.side {
  font-size: 20px;
  font-weight: bold;
}
